import axios from 'axios'
import { Message, MessageBox } from 'element-ui'
import { getToken } from 'common/js/cache'
import * as error from 'api/config'
import routes from '@/router'
import Qs from 'qs'

// axios
const service = axios.create({
  baseURL: process.env.BASE_API, // apibase_url
  timeout: 5000                  // 
})

// request
service.interceptors.request.use(config => {
  if (config.method === 'post') {
    config.data = Qs.stringify(config.data);
  }
  // Do something before request is sent
  let token = getToken()
  if (token) {
    config.headers['X-TOKEN'] = token
  }

  // config.headers['Content-Type'] = 'application/json'
  // config.headers['Content-Type'] = 'application/x-www-form-urlencoded;charset=utf-8'
  return config
}, error => {
  // Do something with request error
  console.log(error) // for debug
  Promise.reject(error)
})

// respone
service.interceptors.response.use(
  response => {
    let res = response.data
    // token
    if (res.code != 0) {
      Message({
        message: res.message,
        type: 'error',
        duration: 5 * 1000
      })
    }
    if (res.code == error.ERROR_TOKEN_EXPIRE || res.code == error.ERROR_TOKEN_ILLEGAL) {
      MessageBox.confirm('', '', { // token
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        routes.push({ name: 'login' })
      })
    } else if (res.code == error.ERROR_AUTHORITY) { // 
      routes.push({ name: '401' })
    }
    return response
  },
  error => {
    console.log('err' + error)// for debug
    Message({
      message: error.message,
      type: 'error',
      duration: 5 * 1000
    })
    return Promise.reject(error)
  }
)

export default service



